import React, { Component } from "react";
import { Container } from "react-bootstrap";
import CardGroup from "react-bootstrap/CardGroup";
import Python from "./ProgrammingLanguagesImages/Python.png";
import RPrograming from "./ProgrammingLanguagesImages/Rlogo.png";
import JavaScript from "./ProgrammingLanguagesImages/JavaScript.png";
import SAS from "./ProgrammingLanguagesImages/SAS.jpg";
import Cpp from "./ProgrammingLanguagesImages/CPlusPlus.png";
import SQL from "./ProgrammingLanguagesImages/SQL.jfif";
import Card from "react-bootstrap/Card";
import "./TechnicalStrengths.css";

class ProgrammingLanguages extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const border = { border: 0 };
    return (
      <Container fluid={true}>
        <h1 className="title">Programming Languages I Know</h1>
        <CardGroup>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={Python}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>Python</Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={RPrograming}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>R</Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={JavaScript}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>Java Script</Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
        <CardGroup>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={SAS}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>SAS</Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={Cpp}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>C++</Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black" style={border}>
            <Card.Img
              variant="top"
              src={SQL}
              className="TechnicalStrengthsCard-img-top"
            />
            <Card.Body>
              <Card.Text>SQL</Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
      </Container>
    );
  }
}

export default ProgrammingLanguages;
