import React, { Component } from "react";
import { Container } from "react-bootstrap";
import Card from "react-bootstrap/Card";
import CardGroup from "react-bootstrap/CardGroup";
import Gear from "./ProjectImages/Gear.png";
import Pipeline from "./ProjectImages/Pipeline.PNG";
import "./Projects.css";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const border = { border: 0 };
    return (
      <Container fluid={true}>
        <h1 className="Projecttitle">Couple of Recent Projects I Worked on</h1>
        <CardGroup>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={Gear}
              className="Projectcard-img-top"
            />
            <Card.Body>
              <Card.Title>Gear Defect Detection</Card.Title>
              <Card.Text>
                Built an object detection model to detect a Defect on Gear
                Faces.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={Pipeline}
              className="Projectcard-img-top"
            />
            <Card.Body>
              <Card.Title>AWS Analytics Pipeline</Card.Title>
              <Card.Text>
                Developed a Machine learning model using AWS Services and
                Deployed it along with a Data Pipeline on AWS.{" "}
              </Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
      </Container>
    );
  }
}

export default Projects;
