import React, { Component } from "react";
import { Container } from "react-bootstrap";
import Typed from 'react-typed';
import "./Banner.css";

class Banner extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }


  greetingTyped = () => {
    // function is invoked once greeting is typed
    this.name.start();
  }

  nameTyped = () => {
    // function is invoked once greeting is typed
    this.designation.start();
  }

  designationTyped = () => {
    // function is invoked once greeting is typed
    this.toolTip.start();
    this.designation.toggle();
  }

  render() {
    const Banner = {
      "padding": 0,
      "margin": 0,
      "maxWidth": "100%",
      "height": "100vh",
      "background": "black"
    };

    return (
      <Container fluid={true} className="Banner" style={Banner}>
        
        <Container className="BannerTitle">
        <Typed
          typedRef={(typed) => { this.greeting = typed; }}
          strings={["<h1>Namaste...!</h1>"]}
          typeSpeed={90}
          loop={false}
          contentType={"html"}
          onComplete={this.greetingTyped}
          showCursor={false}
          className="greeting"/>

        <Typed
          typedRef={(typed) => { this.name = typed; }}
          strings={["<h1>I'm Sai Hari</h1>"]}
          typeSpeed={90}
          loop={false}
          contentType={"html"}
          onComplete={this.nameTyped}
          showCursor={false}
          className="designation"
          stopped={true}/>

        <Typed
          typedRef={(typed) => { this.designation = typed; }}
          strings={["<h3>Data Scientist &amp; Machine Learning Engineer</h3>"]}
          typeSpeed={90}
          loop={false}
          smartBackspace={true}
          contentType={"html"}
          showCursor={false}
          onComplete={this.designationTyped}
          className="quote"
          stopped={true}/>

        <Typed
          typedRef={(typed) => { this.toolTip = typed; }}
          strings={["<p>scroll down to know more about me</p>"]}
          typeSpeed={90}
          loop={false}
          smartBackspace={true}
          contentType={"html"}
          showCursor={false}
          className="toolTip"
          stopped={true}/>
        </Container>
        
        

      </Container>
    );
  }
}

export default Banner;
