import React, { Component } from "react";
import "./About.css";
import { Container } from "react-bootstrap";
import Icon from "./icon.png"


class About extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Container fluid={true}>
        <h1 className="aboutTitle">About Me</h1>
        <Container className="aboutContainer">
        <Container className="aboutTextContainer">
          <p className="body">
            I am an experienced Machine Learning Engineer with a 
            demonstrated history of working in the manufacturing industry. 
            My primary interests lie in the field of Machine Learning and its applications for sustailable development and social good. 
            I am a Strong engineering professional with a Bachelor of Technology - BTech focused in Computer Science from Amrita University, Amritapuri Campus.
          </p>
          </Container>
          <img src={Icon} alt="Italian Trulli"/>
        </Container>
      </Container>
    );
  }
}

export default About;
